import React, {useEffect} from "react";
import {connect} from "react-redux";
import {setTheme} from "../../redux/dark-light-reducer";
import {useDarkMode} from "./useDarkMode";
import {PointerCursor} from "./globalStyles";
import CallTheme from "./CallTheme";

const CallThemeRemote = ({themeBLL, setTheme}) => {
    const [theme, themeToggler] = useDarkMode(themeBLL); //  LocalStorage    
    //   ,        

    const themeTogglerLocal = () => {  // themeTogglerLocal -  
        themeToggler() // 1)  localStorage   
        setTheme(theme) // 2)   themeBLL  
    }
    useEffect(()=>{
        if (theme!==themeBLL) {
            setTheme(theme) //   BLL  ,   localStorage  
        }
    },[theme, themeBLL, setTheme]) // useEffect   theme, themeBLL  setTheme
    return (
        <span>
            <PointerCursor>
                {/*    */}
                <CallTheme themeTogglerLocal={themeTogglerLocal}/>
                {/*     */}
            </PointerCursor>
        </span>)
}

const mapStateToProps = (state) => {
    return {
        themeBLL: state.theme.themeBLL//         CallThemeRemote
        //   
    }
}

export default connect(mapStateToProps, {setTheme})(CallThemeRemote);









